<template>
  <div class="edit-notes">
          <PopupDeleteWrapper
        :hidePopupStatus="popupRemoveHidden"
        @close-popup="popupRemoveToggle(noteIndexOpened)"
        @delete-element="$emit('remove-note', noteIndexOpened); popupRemoveToggle(noteIndexOpened)"
        :header="`Удаление заметки ${note}`"
      >
        <template #popup-delete-content>
          После нажатия на иконку корзины будет удалёна заметка <b>{{note}}</b>
        </template>
      </PopupDeleteWrapper>
    <p v-for="(note, index) in notes" :key="index" class="edit-note">
      {{ note }}
      <AppButtonIcon icon="delete" title="Удалить" size="1.5rem" @button-click="popupRemoveToggle(index)"/>
    </p>
  </div>
</template>

note=element  @button-click="popupRemoveToggle(note)" $emit('removeNote',note)

<script>
import AppButtonIcon from '../AppButtonIcon'
import PopupDeleteWrapper from '../popups/PopupDeleteWrapper'

export default {
  name: 'AppNotesList',

  components: {
    PopupDeleteWrapper,
    AppButtonIcon
  },

  props: {
    notesList: Array,
    notesCount: [String, Number]
  },

  data () {
    return {
      popupRemoveHidden: true,
      note: '',
      noteIndexOpened: ''
    }
  },

  computed: {
    notes () {
      try {
        if (Number(this.$props.notesCount) === 0) {
          return ['Отображение заметок отключено']
        }

        return this.$props.notesList.slice(-this.$props.notesCount)
      } catch (error) {
        return ['Заметки отсутствуют']
      }
    }
  },

  methods: {
    popupRemoveToggle (index) {
      this.noteIndexOpened = index
      this.popupRemoveHidden = !this.popupRemoveHidden
    }
  }
}

</script>

<style scoped>
  .edit-notes {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .edit-note {
    color: gray;
    font-size: 1rem !important;
    display: flex;
    padding: 0;
  }
</style>
